import { Action } from '@ngrx/store';
import { type } from '../utils';

export const ActionTypes = {
  RETRIEVE_PLACES: type('[Place List] Retrieve Places Attempt'),
  RETRIEVE_PLACES_SUCCESS: type('[Place List] Retrieve Places Success'),
  RETRIEVE_PLACES_FAILURE: type('[Place List] Retrieve Places Failure'),
  UPDATE_PAGE: type('[Place List] Update Page')
};

export class RetrievePlacesAction implements Action {
  type = ActionTypes.RETRIEVE_PLACES;

  constructor(public payload: any) { }
}

export class RetrievePlacesSuccessAction implements Action {
  type = ActionTypes.RETRIEVE_PLACES_SUCCESS;

  constructor(public payload: any) { }
}

export class RetrievePlacesFailureAction implements Action {
  type = ActionTypes.RETRIEVE_PLACES_FAILURE;

  constructor(public payload: any) { }
}

export class UpdatePageAction implements Action {
  type = ActionTypes.UPDATE_PAGE;

  constructor(public payload: number) { }
}

export type Actions
  = RetrievePlacesAction
  | RetrievePlacesSuccessAction
  | RetrievePlacesFailureAction
  | UpdatePageAction;



