import '@ngrx/core/add/operator/select';
import { createSelector } from 'reselect';
import { compose } from '@ngrx/core/compose';
import { combineReducers, ActionReducer } from '@ngrx/store';
import { storeFreeze } from 'ngrx-store-freeze';
import { environment } from '../../environments/environment';

import * as fromPlaceList from './place-list';

export interface State {
  placeList: fromPlaceList.State;
}

const reducers = {
  placeList: fromPlaceList.reducer
};

const developmentReducer: ActionReducer<State> = compose(storeFreeze, combineReducers)(reducers);
const productionReducer: ActionReducer<State> = combineReducers(reducers);

export function reducer(state: any, action: any) {
  if (environment.production) {
    return productionReducer(state, action);
  } else {
    return developmentReducer(state, action);
  }
}

export const getPlaceListState = (state: State) => state.placeList;

export const getPlaces = createSelector(getPlaceListState, fromPlaceList.getPlaces);
export const placesListIsLoading = createSelector(getPlaceListState, fromPlaceList.isLoading);
export const placeListPage = createSelector(getPlaceListState, fromPlaceList.page);



