import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'ui-toolbar',
  template: `
  <md-toolbar color="primary">
    <img src="assets/img/app/logo.png" alt="" class="logo">
    <span class="fill"></span>
    <span>
        <md-input-container color="secondary">
            <input mdInput placeholder="Search for.." value="">
        </md-input-container>   
    </span>
    <span>
        <md-select color="primary">
            <md-option [value]="dolar">
                USD 
            </md-option>
            <md-option [value]="euro">
                EUR 
            </md-option>
        </md-select>
    </span>
    <span>
        <md-select color="primary">
            <md-option [value]="es">
                <span>  ES</span>
                <img class="lang" src="assets/img/theme/vendor/es.png" alt=""> 
            </md-option>
            <md-option [value]="fr">
                <span>  FR</span>
                <img class="lang" src="assets/img/theme/vendor/fr.png" alt="">
            </md-option>
            <md-option [value]="us">
                <span>  EN</span>
                <img class="lang" src="assets/img/theme/vendor/us.png" alt="">
            </md-option>
        </md-select>
    </span>
        <span>
        <button md-button end>SIGN IN</button>
    </span>
  </md-toolbar> 
  `,
  styleUrls: ['./ui-toolbar.component.css']
})
export class UiToolbarComponent implements OnInit {

  constructor() { }

  ngOnInit() {
  }

}



