import { Component, OnInit, EventEmitter, Output, ElementRef } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import * as fromRoot from '../../reducers';
import * as placeList from '../../actions/place-list';
import { IGeo, IPlace } from './ui-map.models';
import { GeolocationService } from '../../services/geolocation.service';

@Component({
  selector: 'ui-map',
  template: `<div class="leaflet-maps"></div>`,
  styleUrls: ['./ui-map.component.css']
})
export class UiMapComponent implements OnInit {

  public location: IGeo;
  public map: any;
  public marker: any;
  public places$: Observable<any> = this.store.select(fromRoot.getPlaces);

  @Output() geoEvent: EventEmitter<any> = new EventEmitter;
  private userId: any;
  private place: IPlace;
  
  constructor(
    private store: Store<fromRoot.State>,
    private elementRef: ElementRef,
    private geo: GeolocationService) {
  }

  ngOnInit() {    
    this.store.dispatch( new placeList.RetrievePlacesAction(1));
    this.geo.getLocation().subscribe(loc => {
      this.location = {
        lat: loc.coords.latitude,
        lng: loc.coords.longitude
      };
      this.mapRender();
    }, err => {
      console.log(err);
    });
  }

  private mapRender() {

    let el = this.elementRef.nativeElement.querySelector('.leaflet-maps');
    L.Icon.Default.imagePath = `assets/img/theme/vendor/leaflet`;

    this.map = L.map(el).setView([this.location.lat, this.location.lng], 13);

    L.tileLayer('http://{s}.tiles.wmflabs.org/hikebike/{z}/{x}/{y}.png', {
      maxZoom: 19,
      attribution: ''
    }).addTo(this.map);

    L.tileLayer('http://stamen-tiles-{s}.a.ssl.fastly.net/toposm-features/{z}/{x}/{y}.{ext}', {
      attribution: '',
      subdomains: 'abcd',
      minZoom: 0,
      maxZoom: 20,
      ext: 'png',
      bounds: [[22, -132], [51, -56]],
      opacity: 0.9
    }).addTo(this.map)


    this.marker = new L.marker([this.location.lat, this.location.lng],
      {
        draggable: true,
        title: 'mi posicion ahora mismo',
        opacity: 0.8
      }).addTo(this.map);
    
    this.places$.subscribe((places: IPlace[])=>{
      places.forEach(e => {
        let markerIcon = L.icon({
          iconUrl: 'assets/img/app/icon.png',
          iconSize: [38, 40],
          iconAnchor: [22, 40],
          popupAnchor: [-3, -76],
          shadowSize: [68, 95],
          shadowAnchor: [22, 94]
        });
  
        this.place = e;
  
        let markerplace = new L.marker([
          this.place.geo.lat,
          this.place.geo.lng
        ], {
            draggable: false,
            title: this.place.name,
            opacity: 1,
            icon: markerIcon
          }).addTo(this.map);
      });
    });   

    this.main();
  }

  private main(): void {
    this.marker.on('dragend', (e) => {
      this.location = {
        lat: e.target.getLatLng().lat,
        lng: e.target.getLatLng().lng
      };
      // console.log(this.location);
      this.geoEvent.next(this.location);
    });
  }

}



