import 'rxjs/add/operator/switchMap';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { Injectable } from '@angular/core';
import { Effect, Actions } from '@ngrx/effects';
import { Action } from '@ngrx/store';
import * as placeList from '../actions/place-list';
import { PlacesService } from '../services/places.service';

@Injectable()
export class PlaceListEffects {

  @Effect()
  retrievePlaces$: Observable<Action> = this.actions$
    .ofType(placeList.ActionTypes.RETRIEVE_PLACES)
    .map((action: placeList.RetrievePlacesAction) => action.payload)
    .switchMap((page) =>
      this.placeService.getPlaces()
        .map(places => {      
          return new placeList.RetrievePlacesSuccessAction(places)
        })
        .catch(errors => of(new placeList.RetrievePlacesFailureAction(errors)))
    );

  @Effect()
  updatePage$: Observable<Action> = this.actions$
    .ofType(placeList.ActionTypes.UPDATE_PAGE)
    .map((action: placeList.UpdatePageAction) => action.payload)
    .map(page => new placeList.RetrievePlacesAction(page));

  constructor(
    private actions$: Actions,
    private placeService: PlacesService
  ) { }
}



