import * as placeListActions from '../actions/place-list';

export interface State {
  loading: boolean;
  places: any[];
  page: number;
}

const initialState: State = {
  loading: false,
  places: [],
  page: 1
};

export function reducer(state: State = initialState, action) {
  switch (action.type) {
    case placeListActions.ActionTypes.RETRIEVE_PLACES:
      return Object.assign({}, state, { loading: true, success: false, errors: false });

    case placeListActions.ActionTypes.RETRIEVE_PLACES_SUCCESS:
      const places = action.payload;
      return Object.assign({}, state, { loading: false, success: true, errors: false, places });

    case placeListActions.ActionTypes.RETRIEVE_PLACES_FAILURE:
      return Object.assign({}, state, { loading: false, success: false, errors: action.payload });

    case placeListActions.ActionTypes.UPDATE_PAGE:
      return Object.assign({}, state, { page: action.payload });

    default:
      return state;
  }
}

export const getPlaces = (state: State) => state.places;
export const isLoading = (state: State) => state.loading;
export const page = (state: State) => state.page;



