import { Component, OnInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import * as fromRoot from '../../reducers';

@Component({
  selector: 'ui-aside',
  template: `
    <div class="ui-aside" layout="column" flex style="overflow: scroll;">
      <md-card class="example-card" *ngFor="let place of places$ | async">
        <md-card-header>
          <div md-card-avatar class="example-header-image"></div>
          <md-card-title>{{ place.name }}</md-card-title>
        </md-card-header>
        <img md-card-image class="image" src="http://lorempixel.com/400/200/">
      </md-card>
    </div>
  `,
  styleUrls: ['./ui-aside.component.css']
})
export class UiAsideComponent implements OnInit {
  public places$: Observable<any> = this.store.select(fromRoot.getPlaces);
  constructor(
    private store: Store<fromRoot.State>
  ) { }

  ngOnInit() {
  
  }


}



