import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { MaterialModule } from '@angular/material';
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';

import { AppComponent } from './app.component';
import { reducer } from './reducers';
import { services } from './services';
import { PlaceListEffects } from './effects/place-list';

import { UiMapComponent } from './components/ui-map/ui-map.component';
import { UiAsideComponent } from './components/ui-aside/ui-aside.component';
import { UiToolbarComponent } from './components/ui-toolbar/ui-toolbar.component';
import { UiNavComponent } from './components/ui-nav/ui-nav.component';

@NgModule({
  declarations: [
    AppComponent,
    UiMapComponent,
    UiAsideComponent,
    UiToolbarComponent,
    UiNavComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    MaterialModule,
    StoreModule.provideStore(reducer),
    StoreDevtoolsModule.instrumentOnlyWithExtension(),
    EffectsModule.run(PlaceListEffects)
  ],
  providers: [
    services
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



